}

.nav-items a:before {
  content: "";
  z-index: -1;
  border: 1.5px solid rgba(0, 0, 0, 0);
  border-radius: 36px;
  width: 100%;
  height: 100%;
  transition: border-color .3s, background-color .3s, font-weight .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.nav-item a:hover:before, .nav-item a.active-page:before {
  background-color: #eac645;
  border-color: #eac645;
}

.nav-item a:hover, .nav-item a:focus, .nav-item a.active-page {
