  right: 0;
}

.toggle-switch {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 0 0 auto;
  display: flex;
}

.switch {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#dcdcdc 0%, #4f2ee8 100%);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  width: 20px;
  height: 20px;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 18px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

input:checked + .slider {
  background: linear-gradient(#686868 0%, #4f2ee8 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + .slider:before {
  transform: translateX(-16px);
}

.slider, .circle {
  border-radius: 24px;
}

.circle:before {
  border-radius: 50%;
}

input:checked + .circle:before {
  background-color: #fff;
}

.arrow-right {
